:root {
  --black: #272727;
  /* --black: #334b49; */
  --white: #f1faff;
  --grey: #f7f7f7;
  /* --grey: #f1f1e6; */
  /* --grey: #334b49; */
  --colorLogo: #365f93;
  /* --colorLogo: #202020; */
  /* --colorLogo: #3f4756; */
  --accent1: #00c6bb;
  /* --accent1: #365f93; */
  /* --accent1: #dba21c; */
  /* --accent1: #fff7d6; */
  /* --accent1: #c7798c; */
  /* --accent1: #986bae; */
  /* --accent2: #986bae; */
  /* --accent2: #7868a9; */
  /* --accent2: #8f475a; */
  --accent2: #00c6bb;
}

.material-symbols-outlined {
  font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 24;
  font-size: 48px;
}

.speedDial {
  background: red;
}

body {
  background: var(--grey);
  color: var(--black);
  width: 100%;
  overflow-x: hidden;
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Montserrat', sans-serif;
  margin: none;
  /* padding: 1rem 1rem 2rem 1rem; */
}

.App {
  /* text-align: center; */
  background: var(--grey);
  width: 100%;
  overflow-x: hidden;
}

nav {
  height: 80px;
  /* margin: 1rem; */
  background: var(--white);
  /* border-bottom: 4px solid var(--colorLogo); */
  /* border-radius: 20px; */
  box-shadow: 0 0 2px #272727;
}

#navbarNav {
  width: 100%;
  margin: 0;
  padding: 0;
}

#navbarNav ul {
  width: 100%;
  margin: 0;
  padding: 0 1rem;
}

#navbarNav {
  background: var(--white);
}

nav h1 {
  color: var(--colorLogo);
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.navbar-toggler {
  border: none;
  margin: auto 0;
}

.navbar-toggler:focus {
  outline: none !important;
  box-shadow: none;
}

.hero {
  background: var(--colorLogo);
  color: var(--white);
  padding-bottom: 0rem;
  margin: 80px 0 0 0;
  overflow: hidden;
  /* border-radius: 0 0 150px 0%; */
}

.heroTitle {
  background: var(--colorLogo);
  text-align: left;
  padding: 1rem;
  margin: 0;
  /* font-family: 'Oswald', sans-serif; */
  font-weight: 700;
}

.image {
  width: 100%;
  background: var(--colorLogo);
  /* margin-left: 1rem;
  margin-top: 1rem;
  border-radius: 8px;
  box-shadow: 8px 8px var(--accent1); */
}

.title {
  /* font-weight: 300; */
  padding: 0 1rem 2.5rem 1rem;
  /* font-family: 'Lora', serif; */
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  /* font-family: 'DM Serif Display', serif; */
  margin: 0;
  padding: 2rem 1rem;
}

.serifFont {
  font-family: 'DM Serif Display', serif;
}

.blueTitle {
  background: var(--colorLogo);
  color: var(--white);

  /* border-radius: 8px 8px 0 0; */
}

.whiteTitle {
  background: var(--white);
  color: var(--black);
}

button {
  background-color: var(--accent1);
  /* color: var(--accent1); */
  color: var(--black);
  font-weight: 600;
  padding: 0.75rem;
  margin: 1.3rem 1rem 0 0;
  /* border-radius: 8px; */
  /* border-radius: 50px; */
  border: none;
  /* border: 2px solid var(--black); */
  box-shadow: 2px 2px 5px #979797;
}

.heroCard {
  /* border-bottom: 2px solid var(--black); */
  /* border-radius: 8px; */
  box-shadow: 2px 2px 5px #979797;
  margin: 1rem 0;
  padding: 2rem 1rem;
  background: var(--white);
  color: var(--black);
}

.heroCard h1 {
  font-weight: 700;
}

.testimonialCardContainer {
  padding: 0 1rem;
}

.testimonialCard {
  /* border: 2px solid var(--black); */
  /* padding: 1rem; */
  box-shadow: 2px 2px 5px #979797;
  margin-bottom: 1.5rem;
  border-radius: 8px;
  background: var(--white);
}

.testimonialCard p {
  padding: 1rem;
  margin: 0;
  height: 17rem;
  overflow-y: scroll;
}

.testimonialsAuthor {
  background: var(--accent1);
  color: var(--black);
  padding: 1rem;
  border-radius: 0 0 8px 8px;
}

/* textarea {
  width: 100%;
  padding: 1rem;
  border-radius: 8px;
} */

.input {
  width: 100%;
}

.form-control:focus {
  outline: none;
  border: none;
  box-shadow: 4px 4px var(--accent2);
  background-color: var(--white);
}


.section {
  padding: 1rem;
  padding-bottom: 2rem;
}

.blueSection {
  background: var(--colorLogo);
  /* border-radius: 0 0 8px 8px; */
}

.blueSection button {
  background-color: var(--accent2);
  box-shadow: 2px 2px 5px #979797;
  /* color: var(--white); */
}

.whiteSection {
  background: var(--white);
}

.whiteText {
  color: var(--white);
}

.blackText {
  color: var(--black);
}

.contentText {
  padding-top: 1rem;
  margin: 0;
  font-weight: 500;
}

.marginBottom {
  margin-bottom: 1.5rem;
}

#aboutMe {
  background: var(--colorLogo);
}

#contact {
  background: var(--colorLogo);
}

/* #aboutMe, */
/* #contact { */
/* border-radius: 8px; */
/* box-shadow: 2px 2px 5px #979797; */
/* margin: 0 1rem; */
/* overflow: hidden; */
/* } */

.borderRadius {
  border-radius: 8px;
}

.boxShadow {
  box-shadow: 2px 2px 5px #979797;
}

.margin {
  margin-left: 1rem;
  margin-right: 1rem;
}

#footer {
  background: var(--accent1);
  min-height: 200px;
}

.arrow-down {
  width: 100%;
  height: 0;
  border-left: 100vw solid transparent;
  /* border-right: 100px solid transparent; */

  border-top: 20px solid var(--colorLogo);
}

.arrow-down-2 {
  width: 100%;
  height: 0;
  border-right: 100vw solid transparent;
  /* border-right: 100px solid transparent; */

  border-top: 20px solid var(--colorLogo);
}

.arrow-up {
  width: 100%;
  height: 0;
  border-left: 100vw solid transparent;
  /* border-right: 100px solid transparent; */

  border-bottom: 20px solid var(--colorLogo);
}

.arrow-up-2 {
  width: 100%;
  height: 0;
  border-right: 100vw solid transparent;
  /* border-right: 100px solid transparent; */

  border-bottom: 20px solid var(--colorLogo);
}

.wrapper {
  max-width: 1200px;
  margin: 0;
  padding: 0;
}

.topic {
  display: flex;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .desktopPaddingTop {
    padding-top: 2rem;
  }

  .desktopBorderRadius {
    border-radius: 8px;
  }

  .desktopShadow {
    box-shadow: 2px 2px 5px #979797;
  }

  #contact .wrapper {
    padding: 0 10rem;
  }
}